import {
  END_FETCH_ROUTING_PROBLEM,
  GET_JOBS_LIST,
  GET_ROUTING_PROBLEM,
  GET_TOUR,
  START_FETCH_ROUTING_PROBLEM,
} from "../actionTypes";

const initialState = {
  isLoading: false,
  results: null,
  tours: null,
};

export default function (state = initialState, action) {
  switch (action.type) {
    case START_FETCH_ROUTING_PROBLEM: {
      return {
        ...state,
        isLoading: true,
        tours: null,
      };
    }
    case END_FETCH_ROUTING_PROBLEM: {
      return {
        ...state,
        isLoading: false,
      };
    }
    case GET_ROUTING_PROBLEM: {
      return {
        ...state,
        results: action.payload.json,
      };
    }
    case GET_TOUR: {
      return {
        ...state,
        tours: {
          ...state.tours,
          [action.payload.tour.vehicleId]: action.payload.tour,
        },
      };
    }
    default:
      return state;
  }
}
