import "mapbox-gl/dist/mapbox-gl.css";
import "./styles.css";

import * as turf from "@turf/turf";
import _ from "lodash";
import React, { useEffect, useRef, useState } from "react";
import ReactMapGL, {
  FlyToInterpolator,
  Layer,
  Popup,
  Source,
} from "react-map-gl";
import { TRANSITION_EVENTS } from "react-map-gl";
import { connect } from "react-redux";

import { JOB_FIELDS, MAPBOX } from "../../constants";
import {
  mapboxReady,
  mapboxViewportChange,
  setFormData,
} from "../../redux/actions";
import pinImg from "./images/pin.png";

function MapBox(params) {
  const {
    viewportState,
    mapboxViewportChange,
    mapboxReady,
    jobList,
    mapboxApiAccessToken,
    setFormData,
    routingProblem,
    isMapboxReady,
    tours,
    currentTourId,
    flyToPoint,
    serviceProfiles,
  } = params;

  const [showPopup, setShowPopup] = useState(false);
  const [popupData, setPopupData] = useState(null);
  const [routesGeojson, setRoutesGeojson] = useState(null);
  const [jobListGeojson, setJobListGeojson] = useState(null);
  const [serviceProfilesGeojson, setServiceProfilesGeojson] = useState(null);
  const [currentRouteGeojson, setCurrentRouteGeojson] = useState(null);
  const refMap = useRef(null);

  useEffect(() => {
    const currentTour = tours && tours[currentTourId];

    if (routesGeojson && currentTourId) {
      const filteredTour = _.filter(routesGeojson.features, function (feature) {
        return feature.properties.vehicleId === currentTourId;
      });
      setCurrentRouteGeojson(turf.featureCollection(filteredTour));
    } else {
      setCurrentRouteGeojson(null);
    }
  }, [currentTourId, tours]);

  useEffect(() => {
    if (flyToPoint) {
      doFlyTo(turf.point([flyToPoint.lng, flyToPoint.lat]));
    }
  }, [flyToPoint]);

  useEffect(() => {
    if (currentRouteGeojson) {
      doFlyTo(currentRouteGeojson);
    }
  }, [currentRouteGeojson]);

  useEffect(() => {
    if (jobList && jobList.length > 0 && isMapboxReady) {
      const newJobListGeojson = objToGeoJSON(jobList);
      if (jobListGeojson === null) {
        doFlyTo(newJobListGeojson);
      }
      setJobListGeojson(newJobListGeojson);
    }
  }, [jobList, isMapboxReady]);

  useEffect(() => {
    if (serviceProfiles) {
      setServiceProfilesGeojson(
        objToGeoJSON(_.filter(serviceProfiles, { active: true }))
      );
    }
  }, [serviceProfiles]);

  useEffect(() => {
    if (jobListGeojson) {
      refMap.current.getMap().getSource("job-list") &&
        refMap.current.getMap().getSource("job-list").setData(jobListGeojson);
    }
  }, [jobListGeojson]);

  useEffect(() => {
    if (serviceProfilesGeojson) {
      refMap.current.getMap().getSource("service-profiles") &&
        refMap.current
          .getMap()
          .getSource("service-profiles")
          .setData(serviceProfilesGeojson);
    }
  }, [serviceProfilesGeojson]);

  useEffect(() => {
    const toursCollection = turf.featureCollection([]);
    _.map(tours, (tour) => {
      const lineColor = getRandomColor();
      _.map(tour.sections, (section) => {
        const sectionFeature = turf.lineString(section.polyline.polyline, {
          id: section.id,
          departureTime: section.departure.time,
          arrivalTime: section.arrival.time,
          vehicleId: tour.vehicleId,
          color: lineColor,
        });
        toursCollection.features.push(turf.flip(sectionFeature));
      });
    });
    setRoutesGeojson(toursCollection);
  }, [tours]);

  const doFlyTo = (geojson) => {
    const bboxArray = turf.bbox(geojson);
    const bbox = [
      [bboxArray[0], bboxArray[1]],
      [bboxArray[2], bboxArray[3]],
    ];

    const flyToProps = refMap.current.getMap().cameraForBounds(bbox, {
      padding: { top: 30, bottom: 30, left: 30, right: 30 },
    });

    updateViewport({
      transitionDuration: 2000,
      transitionInterpolator: new FlyToInterpolator(),
      zoom: flyToProps.zoom < 17 ? flyToProps.zoom : 17,
      latitude: flyToProps.center.lat,
      longitude: flyToProps.center.lng,
    });
  };

  const getRandomColor = () => {
    return "#000000".replace(/0/g, function () {
      return (~~(Math.random() * 16)).toString(16);
    });
  };

  const objToGeoJSON = (list) => {
    if (!list) {
      return turf.featureCollection([]);
    }

    const features = _.map(list, (obj) => {
      return turf.point([+obj.longitude, +obj.latitude], obj);
    });
    return turf.featureCollection(features);
  };

  const getLinesFromRoutingProblem = (json) => {
    if (!(json.tours && json.tours.length)) {
      return turf.featureCollection([]);
    }

    const features = _.map(json.tours, (vehicle) => {
      const line = _.map(vehicle.stops, (stop) => {
        return [stop.location.lng, stop.location.lat];
      });
      return turf.lineString(line, { vehicleId: vehicle.vehicleId });
    });

    return turf.featureCollection(features);
  };

  const updateViewport = (viewport) => {
    mapboxViewportChange(viewport);
  };

  const _onClick = (event) => {
    const targetFeture = event.features[0];
    setShowPopup(false);

    console.log(targetFeture);

    if (
      targetFeture &&
      targetFeture.layer.id.indexOf("service-profiles") !== -1
    ) {
      setPopupData(targetFeture.properties);
      setShowPopup(true);
    }

    if (targetFeture && targetFeture.layer.id.indexOf("job-list") !== -1) {
      setPopupData(targetFeture.properties);
      setShowPopup(true);
    }
  };

  return (
    <ReactMapGL
      ref={refMap}
      mapboxApiAccessToken={mapboxApiAccessToken || MAPBOX.API_TOKEN}
      {...viewportState}
      width={"100%"}
      height={"100%"}
      onClick={_onClick}
      onViewportChange={updateViewport}
      onLoad={mapboxReady}
    >
      {routesGeojson && (
        <Source id="routes" type="geojson" data={routesGeojson}>
          <Layer
            id="routes--outline"
            type="line"
            paint={{
              "line-color": "#000000",
              "line-width": 3,
              "line-opacity": currentRouteGeojson ? 0 : 1,
            }}
          />
          <Layer
            id="routes"
            type="line"
            paint={{
              "line-color": ["get", "color"],
              "line-width": 2,
              "line-opacity": currentRouteGeojson ? 0.2 : 1,
            }}
          />
          <Layer
            id="routes--name"
            type="symbol"
            paint={{
              "text-halo-color": "#fff",
              "text-halo-width": 2,
            }}
            layout={{
              "text-field": ["get", "vehicleId"],
              "text-size": 10,
              "symbol-placement": "line",
            }}
          />
        </Source>
      )}
      {currentRouteGeojson && (
        <Source id="current-route" type="geojson" data={currentRouteGeojson}>
          <Layer
            id="current-route--outline"
            type="line"
            paint={{
              "line-color": "#000000",
              "line-width": 3,
            }}
          />
          <Layer
            id="current-route"
            type="line"
            paint={{
              "line-color": ["get", "color"],
              "line-width": 2,
            }}
          />
        </Source>
      )}
      {jobListGeojson && (
        <Source id="job-list" type="geojson" data={jobListGeojson}>
          <Layer
            id="job-list"
            type="circle"
            paint={{
              "circle-radius": 8,
              "circle-color": "#FFFFFF",
              "circle-stroke-width": 2,
              "circle-stroke-color": "#FF3900",
            }}
          />
          <Layer
            id="job-list--id"
            type="symbol"
            paint={{
              "text-halo-color": "#fff",
              "text-halo-width": 1,
            }}
            layout={{
              "text-field": ["get", "JobID"],
              "text-size": 10,
            }}
          />
        </Source>
      )}
      {serviceProfiles && serviceProfiles.length && (
        <Source
          id="service-profiles"
          type="geojson"
          data={serviceProfilesGeojson}
        >
          <Layer
            id="service-profiles"
            type="circle"
            paint={{
              "circle-radius": 8,
              "circle-color": "#FFFFFF",
              "circle-stroke-width": 5,
              //'circle-opacity': ["match", ['get', 'active'], true, 1, 0.25] ,
              "circle-stroke-color": "#078600",
            }}
          />
        </Source>
      )}
      {showPopup && popupData && (
        <Popup
          latitude={+popupData.latitude}
          longitude={+popupData.longitude}
          closeButton={true}
          closeOnClick={false}
          onClose={() => setShowPopup(false)}
          anchor="top"
        >
          <div>
            {_.map(popupData, (value, key) => {
              return (
                <p key={key}>
                  <b>{key}:</b> {String(value)}
                </p>
              );
            })}
          </div>
        </Popup>
      )}
    </ReactMapGL>
  );
}

const mapStateToProps = (state) => {
  return {
    viewportState: state.mapbox.viewport,
    jobList: state.jobList.list,
    routingProblem: state.routingProblem.results,
    tours: state.routingProblem.tours,
    isMapboxReady: state.mapbox.isMapboxReady,
    currentTourId: state.mapbox.currentTourId,
    flyToPoint: state.mapbox.flyToPoint,
    serviceProfiles: state.serviceProfiles.profiles,
  };
};

export default connect(mapStateToProps, {
  mapboxViewportChange,
  mapboxReady,
  setFormData,
})(MapBox);
