import "./styles.css";

import _ from "lodash";
import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";

import { FIELDS_TYPES, JOB_LIST_INDEX_KEY } from "../../constants";
import {
  removeJobField,
  setIsDropdownToggled,
  setJobField,
} from "../../redux/actions";
import DropdownMenu from "../../ui-elements/DropdownMenu";
import trashSVG from "./images/trash.svg";

function JobDetailsForm(params) {
  const { onSubmit, onCancel } = params;

  const jobList = useSelector((state) => state.jobList.list);
  const jobFields = useSelector((state) => state.jobList.jobFields);
  const locationFields = useSelector((state) => state.jobList.locationFields);
  const dispatch = useDispatch();
  const [jobListKeys, setJobListKeys] = useState([]);
  const [customFieldInput, setCustomFieldInput] = useState("");
  const [selectedJobListKeys, setSelectedJobListKeys] = useState([]);

  useEffect(() => {
    if (jobList && jobList.length) {
      setJobListKeys(
        _.filter(Object.keys(jobList[0]), (key) => key !== JOB_LIST_INDEX_KEY)
      );
    }
  }, [jobList]);

  useEffect(() => {
    if (jobList && locationFields && jobFields) {
      const locationFieldsKeys = _.map(locationFields, (field) => field.key);
      const jobFieldsKeys = _.map(jobFields, (field) => field.key);
      const mergedFields = [...locationFieldsKeys, ...jobFieldsKeys];

      setSelectedJobListKeys(
        mergedFields.filter((field) => field !== undefined)
      );
    }
  }, [jobList, locationFields, jobFields]);

  return (
    <div className="csv-details-form-wrapper">
      <Row>
        <Col>
          <div className="csv-details-form_header">
            <div className="step-marker">2</div>
            <div>Job Details</div>
          </div>
        </Col>
      </Row>
      <Row className="csv-details-form_container">
        <Col>
          <div className="vertical-line" />
          <div style={{ width: "100%" }}>
            <Row>
              <Col className="csv-details-form">
                {_.map(jobFields, (field, fieldKey) => (
                  <Row
                    key={fieldKey}
                    noGutters
                    style={{ margin: "5px 0 5px 0" }}
                  >
                    <Col sm="6">
                      <p
                        className={field.required ? "hint-required" : ""}
                        style={{ margin: "5px 10px 5px 0", textAlign: "right" }}
                      >
                        {field.custom && (
                          <img
                            src={trashSVG}
                            width={"13"}
                            className="delete-icon"
                            onClick={() => {
                              dispatch(removeJobField(field.id));
                            }}
                          />
                        )}
                        {field.name}:
                      </p>
                    </Col>
                    <Col sm="6">
                      <DropdownMenu
                        noneItemText={"Select column"}
                        key={fieldKey}
                        onToggle={({ isOpen }) => {
                          dispatch(setIsDropdownToggled(isOpen));
                        }}
                        selectedItem={field.key}
                        selectedItems={selectedJobListKeys}
                        items={jobListKeys}
                        onClick={(newItem) => {
                          dispatch(setJobField({ ...field, key: newItem }));
                        }}
                      />
                    </Col>
                  </Row>
                ))}
                <Row key={"custom"} noGutters style={{ margin: "5px 0 5px 0" }}>
                  <Col sm="6">
                    <Form.Control
                      style={{ width: "95%" }}
                      type="text"
                      placeholder="Custom Attribute"
                      value={customFieldInput}
                      onChange={(e) =>
                        e.target.value.length <= 15 &&
                        setCustomFieldInput(e.target.value)
                      }
                    />
                  </Col>
                  <Col sm="6">
                    <DropdownMenu
                      noneItemText={"Select type"}
                      key={"custom"}
                      disabled={!(customFieldInput.length > 0)}
                      items={Object.keys(FIELDS_TYPES)}
                      selectedItems={selectedJobListKeys}
                      onClick={(newItem) => {
                        dispatch(
                          setJobField({
                            id: `custom_ ${customFieldInput}`,
                            name: customFieldInput,
                            descr: null,
                            type: newItem,
                          })
                        );
                        setCustomFieldInput("");
                      }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
            <Row className="csv-details-form_buttons">
              <Col>
                <Button
                  block
                  onClick={onSubmit}
                  disabled={
                    !!_.find(jobFields, (field) =>
                      field.required ? field.key === undefined : false
                    )
                  }
                >
                  IMPORT
                </Button>
              </Col>
              <Col>
                <Button block variant="link" onClick={onCancel}>
                  CANCEL
                </Button>
              </Col>
            </Row>
          </div>
        </Col>
      </Row>
    </div>
  );
}

export default JobDetailsForm;
