import { SET_FORM_DATA } from "../actionTypes";

const initialState = {
  startDate: null,
  timezone: "Z",
};

export default function (state = initialState, action) {
  switch (action.type) {
    case SET_FORM_DATA: {
      return {
        ...state,
        ...action.payload.params,
      };
    }
    default:
      return state;
  }
}
