import React from "react";
//import { connect, useDispatch } from "react-redux";
import { Col, Container, Row, Spinner, Table } from "react-bootstrap";
import { useSelector } from "react-redux";

import MapBox from "../../components/MapBox/MapBox";

function GeocodePage(params) {
  const {} = params;
  const jobsList = useSelector((state) => state.jobList.db);

  //console.log(jobsList);

  return (
    <Container>
      <Row>
        <Col>
          {!jobsList.length &&
            <Spinner animation="border" />
          }
          {!!jobsList.length &&
            <Table striped bordered hover>
              <thead>
                <tr>
                  <td>JobID</td>
                  <td>StreetAddress</td>
                  <td>City</td>
                  <td>Zip</td>
                  <td>Coordinates</td>
                </tr>
              </thead>
              <tbody>
              {jobsList.map((item, key) => {
                return (
                  <tr key={key} className={item.coordinates === "error" ? "error" : ""}>
                    <td>{item.JobID}</td>
                    <td>{item.StreetAddress}</td>
                    <td>{item.City}</td>
                    <td>{item.Zip}</td>
                    <td>
                      {item.coordinates === undefined &&
                        <Spinner animation="border" size="sm" />
                      }
                      {item.coordinates !== undefined &&
                        item.coordinates
                      }
                    </td>
                  </tr>
                );
              })}
              </tbody>
            </Table>
          }
        </Col>
      </Row>
    </Container>
  );
}

export default GeocodePage;
