import {
  ADD_JOB_LIST_VALUE,
  ADD_SERVICE_PROFILE,
  DELETE_SERVICE_PROFILE,
  EDIT_SERVICE_PROFILE,
  END_FETCH_ROUTING_PROBLEM,
  GET_DB_DATA,
  GET_JOBS_LIST,
  GET_ROUTING_PROBLEM,
  GET_TOUR,
  MAPBOX_READY,
  MAPBOX_VIEWPORT_CHANGE,
  MOVE_TO_POINT,
  REMOVE_JOB_FIELD,
  REMOVE_JOB_LIST_VALUE,
  RESIZE_WINDOW,
  SET_CURRENT_TOUR,
  SET_EDIT_SERVICE_PROFILE,
  SET_ERROR_MESSAGE,
  SET_FORM_DATA,
  SET_IS_BATCH_EDITING,
  SET_IS_DROPDOWN_TOGGLED,
  SET_JOB_FIELD,
  SET_JOB_LIST_VALUE,
  SET_LOCATION_FIELD,
  SHOW_JOBS_EDITOR_MODAL,
  SHOW_PROFILE_FORM_MODAL,
  SHOW_ROUTING_PROBLEM_RESULT,
  START_FETCH_ROUTING_PROBLEM,
} from "./actionTypes";

export const getDBData = (data) => ({
  type: GET_DB_DATA,
  payload: {
    data,
  },
});

export const mapboxReady = () => ({
  type: MAPBOX_READY,
  payload: {},
});

export const mapboxViewportChange = (viewport) => ({
  type: MAPBOX_VIEWPORT_CHANGE,
  payload: {
    viewport,
  },
});

export const changeWindowSize = (width, height) => ({
  type: RESIZE_WINDOW,
  payload: {
    width,
    height,
  },
});

export const getJobsList = (jobList) => ({
  type: GET_JOBS_LIST,
  payload: {
    jobList,
  },
});

export const setFormData = (params) => ({
  type: SET_FORM_DATA,
  payload: {
    params,
  },
});

export const getRoutingProblem = (json) => ({
  type: GET_ROUTING_PROBLEM,
  payload: {
    json,
  },
});

export const getTour = (tour) => ({
  type: GET_TOUR,
  payload: {
    tour,
  },
});

