import _ from "lodash";

import {
  ADD_SERVICE_PROFILE,
  DELETE_SERVICE_PROFILE,
  EDIT_SERVICE_PROFILE,
  MAPBOX_READY,
  SET_EDIT_SERVICE_PROFILE,
} from "../actionTypes";

const initialState = {
  editServiceProfile: null,
  profiles: [
    {
      name: "Plumber",
      skills: "skill1,skill2,skill3",
      longitude: -93.4376969129225,
      latitude: 44.93210342318257,
      timeWindow: ["06:00", "21:00"],
      vehicleCount: 3,
      vehicleType: 1,
      active: true,
      mustGoBack: true,
      fixedCost: 22,
      distanceCost: 1,
      timeCost: 2,
      capacity: 20,
    },
    {
      name: "Electrician",
      skills: "skill1,skill2,skill3",
      longitude: -93.41010833483658,
      latitude: 44.896012096005286,
      timeWindow: ["06:00", "21:00"],
      vehicleCount: 5,
      vehicleType: 3,
      active: true,
      mustGoBack: true,
      fixedCost: 22,
      distanceCost: 1,
      timeCost: 1,
      capacity: 15,
    },
  ],
};

export default function (state = initialState, action) {
  switch (action.type) {
    case ADD_SERVICE_PROFILE: {
      return {
        ...state,
        editServiceProfile: null,
        profiles: [...state.profiles, action.payload.profile],
      };
    }
    case EDIT_SERVICE_PROFILE: {
      return {
        ...state,
        editServiceProfile: null,
        profiles: _.map(state.profiles, (profile, key) => {
          if (profile.name === action.payload.name) {
            return {
              ...profile,
              ...action.payload.params,
            };
          } else {
            return profile;
          }
        }),
      };
    }
    case DELETE_SERVICE_PROFILE: {
      return {
        ...state,
        profiles: _.filter(state.profiles, (profile) => {
          return profile.name !== action.payload.profile.name;
        }),
      };
    }
    case SET_EDIT_SERVICE_PROFILE: {
      return {
        ...state,
        editServiceProfile: action.payload.profile,
      };
    }
    default:
      return state;
  }
}
