//import {} from '../../redux/actions';
import "../styles.css";

import _ from "lodash";
import React from "react";
import { Dropdown, DropdownButton } from "react-bootstrap";

export default function DropdownMenu(params) {
  const {
    selectedItem,
    selectedItems,
    noneItemText,
    items,
    onClick,
    style,
    disabled,
    onToggle = () => {},
  } = params;

  const _onClick = ({ target }) => {
    const newValue =
      target.dataset.active === "true" ? undefined : target.dataset.value;
    onClick(newValue);
  };

  return (
    <DropdownButton
      disabled={disabled}
      onToggle={(isOpen, event, metadata) => {
        onToggle({ isOpen, event, metadata });
      }}
      variant={selectedItem ? "primary" : "secondary"}
      title={selectedItem ? selectedItem : noneItemText}
    >
      {selectedItem && (
        <Dropdown.Item style={style} key={"undefined"} onClick={_onClick}>
          <span className={"font-italic text-secondary"}>None</span>
        </Dropdown.Item>
      )}
      {items.length > 0 &&
        _.map(items, (item, key) => (
          <Dropdown.Item
            style={style}
            key={key}
            active={selectedItem && item === selectedItem}
            disabled={
              !(selectedItem && item === selectedItem) &&
              selectedItems.find((item_selected) => item === item_selected) !==
                undefined
            }
            data-value={item}
            data-active={selectedItem && item === selectedItem}
            onClick={_onClick}
          >
            {item}
          </Dropdown.Item>
        ))}
      {!items.length && <Dropdown.Header>No available fields</Dropdown.Header>}
    </DropdownButton>
  );
}
