import { dynamoListStreams, listDynamoDB, s3Upload } from "aws";
import UploadFileArea from "components/UploadFileArea";
import * as CSV from "csv-string";
import jqueryCSV from "jquery-csv";
import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { getDBData, getJobsList } from "redux/actions";
import { routePath } from "routing";

import { GRID_ID } from "../../constants";

function UploadPage(params) {
  const {} = params;

  const dispatch = useDispatch();
  let history = useHistory();

  const onDropAccepted = (file) => {
    window.localStorage.removeItem("grid" + GRID_ID);

    s3Upload(file).then((file_data) => {
      console.log(file_data);
      let counter = 0;
      const interval = setInterval(() => {
        counter++;
        listDynamoDB({ file_name: file_data.Key }).then((data) => {
          console.log(data);
          dispatch(getDBData(data.Items));
          const geocodedCount = data.Items.filter((item) => {
            return item.coordinates !== undefined;
          }).length;
          //console.log(geocodedCount);
          if (counter >= 10 && geocodedCount === data.Items.length) {
            clearInterval(interval);
          }
        });
      }, 1500);
    });

    file.text().then((text) => {
      try {
        const separator = CSV.detect(text);

        let parsedCSV = jqueryCSV.toObjects(text, { separator });

        parsedCSV = parsedCSV.filter(
          (row) => Object.values(row).join("").length > 0
        );
        parsedCSV = parsedCSV.map((row) => {
          delete row[""];
          return row;
        });

        dispatch(getJobsList(parsedCSV));
        history.push(routePath.geocode);
      } catch (error) {
        console.log(error);
      }
    });
  };

  return (
    <Container>
      <Row>
        <Col>
          <UploadFileArea onDropAccepted={onDropAccepted} />
        </Col>
      </Row>
    </Container>
  );
}

export default UploadPage;
