import "./styles.css";

import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { routePath } from "routing";

import Modal from "../Modal/Modal";
import JobDetailsForm from "./JobDetailsForm";
import LocationDetailsForm from "./LocationDetailsForm";

function CSVDetailsForms(params) {
  const {} = params;

  let history = useHistory();
  const [currentStep, setCurrentStep] = useState(0);
  const [isModalSaveDataOpen, setIsModalSaveDataOpen] = useState(false);
  const [isModalCancelOpen, setIsModalCancelOpen] = useState(false);
  const isBatchEditing = useSelector((state) => state.jobList.isBatchEditing);
  const isDropdownToggled = useSelector(
    (state) => state.jobList.isDropdownToggled
  );

  useEffect(() => {
    if (isBatchEditing && isDropdownToggled) {
      setIsModalOpen(true);
    }
  }, [isBatchEditing, isDropdownToggled]);

  const _onCancel = () => setIsModalCancelOpen(true);

  return (
    <>
      <Row
        style={{
          overflowY: "auto",
          height: "100vh",
        }}
      >
        <Col>
          {currentStep > 0 && (
            <Row>
              <Col>
                <Button
                  variant="link"
                  onClick={() =>
                    setCurrentStep(
                      (current_currentStep) => current_currentStep - 1
                    )
                  }
                >
                  {" BACK"}
                </Button>
              </Col>
            </Row>
          )}
          {currentStep === 0 && (
            <LocationDetailsForm
              onSubmit={() => {
                setCurrentStep(1);
              }}
              onCancel={_onCancel}
            />
          )}
          {currentStep === 1 && (
            <JobDetailsForm
              onSubmit={() => {
                history.push(routePath.geocode);
              }}
              onCancel={_onCancel}
            />
          )}
        </Col>
      </Row>
      <Modal
        isOpen={isModalSaveDataOpen}
        onHide={() => setIsModalSaveDataOpen(false)}
        titleText={"Warning: unsaved data"}
      >
        <Row>
          <Col className={"text-center mb-3"}>
            <h5>Please, save or cancel changes in grid</h5>
          </Col>
        </Row>
        <Row>
          <Col>
            <Button
              block
              variant={"secondary"}
              onClick={() => {
                setIsModalOpen(false);
              }}
            >
              Got it
            </Button>
          </Col>
        </Row>
      </Modal>
      <Modal
        isOpen={isModalCancelOpen}
        onHide={() => setIsModalCancelOpen(false)}
        titleText={"Warning:"}
      >
        <Row>
          <Col className={"text-center mb-3"}>
            <h5>Are you sure you want to close the editor?</h5>
            <h5>hanges will not be saved.</h5>
          </Col>
        </Row>
        <Row>
          <Col>
            <Button
              block
              variant={"danger"}
              onClick={() => {
                setIsModalCancelOpen(false);
                history.push(routePath.upload);
              }}
            >
              Yes
            </Button>
          </Col>
          <Col>
            <Button
              block
              variant={"secondary"}
              onClick={() => {
                setIsModalCancelOpen(false);
              }}
            >
              Cancel
            </Button>
          </Col>
        </Row>
      </Modal>
    </>
  );
}

export default CSVDetailsForms;
