import {
  GET_ROUTING_PROBLEM,
  RESIZE_WINDOW,
  SET_ERROR_MESSAGE,
  SHOW_JOBS_EDITOR_MODAL,
  SHOW_PROFILE_FORM_MODAL,
  SHOW_ROUTING_PROBLEM_RESULT,
} from "../actionTypes";

const initialState = {
  width: 0,
  height: 0,
  errorMessage: null,
  isRoutingProblemResultShown: false,
  isJobsEditorModalShown: false,
  isProfileFormModalShown: false,
};

export default function (state = initialState, action) {
  switch (action.type) {
    case RESIZE_WINDOW: {
      return {
        ...state,
        width: action.payload.width,
        height: action.payload.height,
      };
    }
    case SHOW_ROUTING_PROBLEM_RESULT: {
      return {
        ...state,
        isRoutingProblemResultShown: action.payload.status,
      };
    }
    case GET_ROUTING_PROBLEM: {
      return {
        ...state,
        isRoutingProblemResultShown: true,
      };
    }
    case SET_ERROR_MESSAGE: {
      return {
        ...state,
        errorMessage: action.payload.params,
      };
    }
    case SHOW_JOBS_EDITOR_MODAL: {
      return {
        ...state,
        isJobsEditorModalShown: action.payload.status,
      };
    }
    case SHOW_PROFILE_FORM_MODAL: {
      return {
        ...state,
        isProfileFormModalShown: action.payload.status,
      };
    }
    default:
      return state;
  }
}
