import _ from "lodash";

import {
  JOB_FIELDS,
  JOB_LIST_INDEX_KEY,
  LOCATION_FIELDS,
} from "../../constants";
import {
  ADD_JOB_LIST_VALUE,
  GET_DB_DATA,
  GET_JOBS_LIST,
  REMOVE_JOB_FIELD,
  REMOVE_JOB_LIST_VALUE,
  SET_IS_BATCH_EDITING,
  SET_IS_DROPDOWN_TOGGLED,
  SET_JOB_FIELD,
  SET_JOB_LIST_VALUE,
  SET_LOCATION_FIELD,
} from "../actionTypes";

const initialState = {
  isBatchEditing: false,
  isDropdownToggled: false,
  list: null,
  db: [],
  locationFields: LOCATION_FIELDS,
  jobFields: JOB_FIELDS,
};

export default function (state = initialState, action) {
  switch (action.type) {
    case GET_DB_DATA: {
      return {
        ...state,
        db: action.payload.data,
      };
    }
    case GET_JOBS_LIST: {
      return {
        ...state,
        list: _.map(action.payload.jobList, (job, key) => ({
          ...job,
          [JOB_LIST_INDEX_KEY]: key,
        })),
        locationFields: LOCATION_FIELDS,
        jobFields: JOB_FIELDS,
      };
    }
    case ADD_JOB_LIST_VALUE: {
      return {
        ...state,
        list: [
          {
            ...action.payload.params,
            [JOB_LIST_INDEX_KEY]: state.list.length,
          },
          ...state.list,
        ],
      };
    }
    case REMOVE_JOB_LIST_VALUE: {
      return {
        ...state,
        list: _.filter(
          state.list,
          (job) => job[JOB_LIST_INDEX_KEY] !== action.payload.index
        ),
      };
    }
    case SET_JOB_LIST_VALUE: {
      return {
        ...state,
        list: _.map(state.list, (job, index) => {
          if (index === action.payload.index) {
            return {
              ...job,
              ...action.payload.params,
            };
          } else {
            return job;
          }
        }),
      };
    }
    case SET_LOCATION_FIELD: {
      return {
        ...state,
        locationFields: {
          ...state.locationFields,
          [action.payload.field.id]: action.payload.field,
        },
      };
    }
    case SET_JOB_FIELD: {
      return {
        ...state,
        jobFields: {
          ...state.jobFields,
          [action.payload.field.id]: action.payload.field,
        },
      };
    }
    case REMOVE_JOB_FIELD: {
      return {
        ...state,
        jobFields: _.filter(
          state.jobFields,
          (field) => field.id !== action.payload.id
        ),
      };
    }
    case SET_IS_BATCH_EDITING: {
      return {
        ...state,
        isBatchEditing: action.payload.status,
      };
    }
    case SET_IS_DROPDOWN_TOGGLED: {
      return {
        ...state,
        isDropdownToggled: action.payload.status,
      };
    }
    default:
      return state;
  }
}
