import AWS from "aws-sdk";
import { AWS as constAWS } from "constants.js";

AWS.config.region = constAWS.region;
AWS.config.credentials = new AWS.CognitoIdentityCredentials({
  IdentityPoolId: constAWS.IdentityPoolId,
});
AWS.config.apiVersions = {
  dynamodbstreams: "2012-08-10",
  s3: "2006-03-01",
};

export const S3Bucket = new AWS.S3({
  params: { Bucket: constAWS.bucketName },
});

export const s3Upload = (file) =>
  new Promise((resolve, reject) => {
    if (file) {
      const fileName = Date.now() + " - " + file.name;
      S3Bucket.upload(
        {
          Key: fileName,
          Body: file,
          ACL: "public-read",
        },
        {},
        function (err, data) {
          if (err) reject(err);
          else resolve(data);
        }
      );
    } else {
      reject(Error("Invalid file"));
    }
  });

export const dynamoDBStreams = new AWS.DynamoDBStreams({
  region: constAWS.region,
  credentials: new AWS.CognitoIdentityCredentials({
    IdentityPoolId: constAWS.IdentityPoolId,
  }),
});

export const dynamoListStreams = () =>
  new Promise((resolve, reject) => {
    const params = {
      TableName: "DeliveryApp_UploadedFiles",
    };

    dynamoDBStreams.listStreams(params, function (err, data) {
      if (err) reject(err);
      else resolve(data);
    });
  });

export const dynamoGetShardIterator = () =>
  new Promise((resolve, reject) => {
    const params = {
      TableName: "DeliveryApp_UploadedFiles",
    };

    dynamoDBStreams.getShardIterator(params, function (err, data) {
      if (err) reject(err);
      else resolve(data);
    });
  });

export const dynamoDBClient = new AWS.DynamoDB.DocumentClient();

export const listDynamoDB = ({ file_name }) =>
  new Promise((resolve, reject) => {
    const params = {
      TableName: "DeliveryApp_UploadedFiles",
      KeyConditionExpression: "#yr = :yyyy",
      ExpressionAttributeNames: {
        "#yr": "file_name",
      },
      ExpressionAttributeValues: {
        ":yyyy": file_name,
      },
    };

    dynamoDBClient.query(params, function (err, data) {
      if (err) {
        reject(err);
      } else {
        resolve(data);
      }
    });
  });
