/* eslint-disable new-cap */
import "bootstrap/dist/css/bootstrap.css";

import awsconfig from "aws/aws-exports";
import Amplify from "aws-amplify";
import CryptoJS from "crypto-js";
import $ from "jquery";
import Cookies from "js-cookie";
import React from "react";
import { Container } from "react-bootstrap";
import { connect } from "react-redux";
import { Redirect, Route, Switch } from "react-router-dom";

import csvFile from "./data/Job_list.csv";
import OAuth from "./oauth-1.0a";
import GeocodePage from "./pages/GeocodePage";
import ImportPage from "./pages/ImportPage";
import MapPage from "./pages/MapPage";
import UploadPage from "./pages/UploadPage";
import { changeWindowSize } from "./redux/actions";
import { fetchJobsListCSV } from "./redux/fetchers";
import { routePath } from "./routing";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.props = props;
  }

  componentDidMount() {
    const changeWindowSizeState = () => {
      const width = $(window).width();
      const height = $(window).height();
      this.props.changeWindowSize(width, height);
    };

    changeWindowSizeState();

    $(window).on("resize", function () {
      changeWindowSizeState();
    });

    this.props.fetchJobsListCSV(csvFile);

    Amplify.configure(awsconfig);

    const bearer_token = Cookies.get("bearer_token");

    if (!bearer_token) {
      // Initialize
      const oauth = new OAuth({
        consumer: {
          key: "vqiFCvO6q-k2d_hWaLrZEg",
          secret:
            "qGafumAC3TJm9u6hziigM3uU3bBOY_WGPAX85pOmwumfJzqzDo7RSPLeaGM6-ZKBO_7f9zM_y9IvJJb8vrpd6Q",
        },
        signature_method: "HMAC-SHA256",
        hash_function(base_string, key) {
          return CryptoJS.HmacSHA256(base_string, key).toString(
            CryptoJS.enc.Base64
          );
        },
      });

      const request_data = {
        url: "https://account.api.here.com/oauth2/token",
        method: "POST",
        data: { grant_type: "client_credentials" },
      };

      $.ajax({
        url: request_data.url,
        type: request_data.method,
        headers: oauth.toHeader(oauth.authorize(request_data)),
        data: request_data.data,
      }).done(function (data) {
        Cookies.set("bearer_token", data.access_token, { expires: 1 });
      });
    }
  }

  render() {
    return (
      <Container fluid className="delivery-app">
        <Switch>
          <Route exact path={routePath.upload} component={UploadPage} />
          <Route exact path={routePath.import} component={ImportPage} />
          <Route exact path={routePath.map} component={MapPage} />
          <Route exact path={routePath.geocode} component={GeocodePage} />
          <Route exact path="/">
            <Redirect to={routePath.upload} />
          </Route>
        </Switch>
      </Container>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    errorMessage: state.window.errorMessage,
    isRoutingProblemResultShown: state.window.isRoutingProblemResultShown,
    isJobsEditorModalShown: state.window.isJobsEditorModalShown,
    isProfileFormModalShown: state.window.isProfileFormModalShown,
    editServiceProfile: state.serviceProfiles.editServiceProfile,
    timezone: state.formData.timezone,
  };
};

export default connect(mapStateToProps, { changeWindowSize, fetchJobsListCSV })(
  App
);
