import "./styles.css";
import "@syncfusion/ej2/material.css";

import {
  Aggregate,
  AggregateColumnDirective,
  AggregateColumnsDirective,
  AggregateDirective,
  AggregatesDirective,
  ColumnDirective,
  ColumnMenu,
  ColumnsDirective,
  Edit,
  Filter,
  GridComponent,
  Inject,
  Page,
  Resize,
  Sort,
  Toolbar,
} from "@syncfusion/ej2-react-grids";
import _ from "lodash";
import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import { FIELDS_TYPES, GRID_ID, JOB_LIST_INDEX_KEY } from "../../constants";
import {
  addJobJistValue,
  removeJobJistValue,
  setIsBatchEditing,
  setJobJistValue,
} from "../../redux/actions";

export default function DataGrid(params) {
  const {} = params;

  const _gridRef = useRef();
  const dispatch = useDispatch();
  const [isGridReady, setIsGridReady] = useState(false);
  const jobList = useSelector((state) => state.jobList.list);
  const locationFields = useSelector((state) => state.jobList.locationFields);
  const jobFields = useSelector((state) => state.jobList.jobFields);
  const fieldsParams = [
    ...Object.values(locationFields),
    ...Object.values(jobFields),
  ];

  const isFieldValid = (column, data) => {
    if (column === undefined) return true;
    const { field, validationRules } = column;
    let isValid = true;
    if (field && validationRules) {
      const currentField = fieldsParams.find(
        (fieldParams) => fieldParams.key === field
      );
      if (currentField) {
        const isRequiredValid = currentField.required
          ? data[currentField.key] && data[currentField.key].length > 0
          : true;
        const isValueValid = validationRules.regex[0].test(
          String(data[currentField.key])
        );
        if (!isValueValid || !isRequiredValid) {
          isValid = false;
        }
      }
    }
    return isValid;
  };

  const getCellStyle = ({ column, data, cell }) => {
    if (column) {
      if (!isFieldValid(column, data)) {
        cell.classList.add("cell-error");
      }
    }
  };

  const getColumnValidationRules = (field) => {
    if (field && field.key) {
      return {
        ...FIELDS_TYPES[field.type].validationRules,
        required: field.required === true,
      };
    }
    return undefined;
  };

  const getColumnSortComparer = (field) => {
    if (
      field &&
      field.key &&
      FIELDS_TYPES[field.type].sortComparer !== undefined
    ) {
      return (reference, comparer, referenceRow, comparerRow) => {
        const currentCol = _gridRef.current.getColumnByField(field.key);
        const sortResult = FIELDS_TYPES[field.type].sortComparer(
          reference,
          comparer,
          referenceRow,
          comparerRow
        );
        return currentCol.sortDirection === "Descending"
          ? sortResult
          : sortResult * -1;
      };
    }
    return undefined;
  };

  const getFieldParamsByKey = (key) => {
    if (!key) return undefined;
    return fieldsParams.find((field) => field.key === key);
  };

  const clearColumnStorage = () => {
    let localStorage = JSON.parse(
      window.localStorage.getItem("grid" + GRID_ID)
    );
    if (localStorage) {
      delete localStorage["columns"];
      window.localStorage.setItem(
        "grid" + GRID_ID,
        JSON.stringify(localStorage)
      );
    }
  };

  const setCurrentPageStorage = (page) => {
    let localStorage = JSON.parse(
      window.localStorage.getItem("grid" + GRID_ID)
    );
    if (localStorage) {
      localStorage.pageSettings.currentPage = page;

      window.localStorage.setItem(
        "grid" + GRID_ID,
        JSON.stringify(localStorage)
      );
    }
  };

  //clear columns state for enablePersistence
  useEffect(() => {
    clearColumnStorage();
  }, []);

  useEffect(() => {
    if (isGridReady && _gridRef.current) {
      _.map(_gridRef.current.columns, (column) => {
        const fieldParams = getFieldParamsByKey(column.field);
        column.validationRules = getColumnValidationRules(fieldParams);
        column.sortComparer = getColumnSortComparer(fieldParams);
      });

      _gridRef.current.componentRefresh();
      _gridRef.current.refreshHeader();
      _gridRef.current.clearFiltering();
      _gridRef.current.clearSorting();
    }
  }, [jobFields, locationFields]);

  useEffect(() => {
    if (isGridReady && _gridRef.current) {
      _gridRef.current.autoFitColumns();
      //update component height
      setTimeout(() => _gridRef.current.componentRefresh(), 250);
    }
  }, [isGridReady]);

  useEffect(() => {
    if (jobList) {
      _gridRef.current.dataSource = [...jobList];
      _gridRef.current.refresh();
    }
  }, [jobList]);

  if (!jobList) return null;

  const jobListKeys = Object.keys(jobList[0]);

  const _beforeBatchSave = ({ batchChanges }) => {
    _.map(batchChanges.addedRecords, (job) => {
      dispatch(addJobJistValue(job));
    });

    _.map(batchChanges.changedRecords, (job) => {
      dispatch(setJobJistValue(job, job[JOB_LIST_INDEX_KEY]));
    });

    _.map(batchChanges.deletedRecords, (job) => {
      dispatch(removeJobJistValue(job[JOB_LIST_INDEX_KEY]));
    });

    dispatch(setIsBatchEditing(false));
  };

  const _columnMenuOpen = ({ items, column }) => {
    const errorsCount = getErrorsFromColumn(column).length;
    const errorsFilterItem = items.find(({ id }) => id === "errorsToTop");
    errorsFilterItem.hide = errorsCount === 0;
  };

  const _dataBoundEvent = () => {
    _gridRef.current.enableToolbarItems(
      ["clearFilters"],
      Object.keys(_gridRef.current.filterModule.actualPredicate).length > 0
    );
  };

  const _columnMenuClick = ({ item, column }) => {
    if (item.id === "errorsToTop") {
      _gridRef.current.clearFiltering();
      _gridRef.current.filterByColumn(
        column.field,
        "equal",
        getErrorsFromColumn(column)
      );
    }
  };

  const _toolbarClick = ({ item }) => {
    if (item.id === "clearFilters") {
      _gridRef.current.clearFiltering();
    }
  };

  const _footerTemplate = ({ Custom }) => {
    if (Custom === 0) return null;
    return <span className={"header-error"}>Errors: {Custom}</span>;
  };

  const _aggregateErrorsCount = (field) => {
    return () =>
      getErrorsFromColumn(_gridRef.current.getColumnByField(field)).length;
  };

  const _actionBegin = ({ requestType, currentPage }) => {
    if (requestType === "paging") {
      setCurrentPageStorage(currentPage);
    }
  };

  const getErrorsFromColumn = (column) => {
    let errorList = [];
    _gridRef.current.props.dataSource.map((job) => {
      if (!isFieldValid(column, job)) {
        errorList.push(job[column.field]);
      }
    });
    return errorList;
  };

  return (
    <GridComponent
      id={GRID_ID}
      ref={_gridRef}
      created={() => setIsGridReady(true)}
      actionBegin={_actionBegin}
      batchAdd={() => dispatch(setIsBatchEditing(true))}
      batchDelete={() => dispatch(setIsBatchEditing(true))}
      batchCancel={() => dispatch(setIsBatchEditing(false))}
      beforeBatchSave={_beforeBatchSave}
      dataBound={_dataBoundEvent}
      cellSave={({ previousValue, value }) => {
        if (previousValue !== value) {
          dispatch(setIsBatchEditing(true));
        }
      }}
      pageSettings={{
        pageSize: 30,
        pageSizes: [30, 60, 100],
      }}
      dataSource={[...jobList]}
      enablePersistence={true}
      allowFiltering={true}
      allowSorting={true}
      height={"100%"}
      allowPaging={true}
      filterSettings={{ type: "Excel" }}
      toolbar={[
        "Add",
        "Delete",
        "Update",
        "Cancel",
        {
          text: "Clear Filters",
          tooltipText: "Clear Filters",
          prefixIcon: "fltr-can2",
          id: "clearFilters",
          disabled: true,
        },
        "Search",
      ]}
      toolbarClick={_toolbarClick}
      editSettings={{
        allowEditing: true,
        allowAdding: true,
        allowDeleting: true,
        mode: "Batch",
      }}
      columnMenuItems={[
        {
          text: "Filter by errors",
          id: "errorsToTop",
        },
        "SortAscending",
        "SortDescending",
        "AutoFit",
        "Filter",
      ]}
      columnMenuOpen={_columnMenuOpen}
      columnMenuClick={_columnMenuClick}
      allowResizing={true}
      allowTextWrap={false}
      showColumnMenu={true}
      queryCellInfo={getCellStyle}
    >
      <Inject
        services={[
          Filter,
          Sort,
          Page,
          Edit,
          Toolbar,
          Resize,
          ColumnMenu,
          Aggregate,
        ]}
      />
      <ColumnsDirective>
        <ColumnDirective type="checkbox" />
        {_.map(jobListKeys, (jobListKey) => (
          <ColumnDirective
            key={jobListKey}
            field={jobListKey}
            validationRules={getColumnValidationRules(
              getFieldParamsByKey(jobListKey)
            )}
            sortComparer={getColumnSortComparer(
              getFieldParamsByKey(jobListKey)
            )}
            lockColumn={jobListKey === JOB_LIST_INDEX_KEY}
            visible={jobListKey !== JOB_LIST_INDEX_KEY}
          />
        ))}
      </ColumnsDirective>
      <AggregatesDirective>
        <AggregateDirective>
          <AggregateColumnsDirective>
            {_.map(jobListKeys, (jobListKey) => (
              <AggregateColumnDirective
                key={jobListKey}
                field={jobListKey}
                type={"Custom"}
                footerTemplate={_footerTemplate}
                customAggregate={_aggregateErrorsCount(jobListKey)}
              />
            ))}
          </AggregateColumnsDirective>
        </AggregateDirective>
      </AggregatesDirective>
    </GridComponent>
  );
}
