import * as turf from "@turf/turf";
import { API, graphqlOperation } from "aws-amplify";
import {
  getDeliveryAppUploadedFiles,
  listDeliveryAppUploadedFiles,
} from "graphql/queries";
import jqueryCSV from "jquery-csv";
import Cookies from "js-cookie";
import _ from "lodash";
import moment from "moment";

import { VEHICLES } from "../constants";
import polylineDecode from "../flexible-polyline";
import {
  endFetchRoutingProblem,
  getJobsList,
  getRoutingProblem,
  getTour,
  setErrorMessage,
  setFormData,
  startFetchRoutingProblem,
} from "./actions";
var tzlookup = require("tz-lookup");

export const getDBRecord = (params) => {
  return API.graphql(graphqlOperation(getDeliveryAppUploadedFiles, params));
};

export const listDBRecords = (params) => {
  return API.graphql(graphqlOperation(listDeliveryAppUploadedFiles, params));
};

export const fetchJobsListCSV = (url) => {
  return (dispatch) => {
    fetch(url)
      .then((response) => response.text())
      .then((csv) => {
        dispatch(getJobsList(jqueryCSV.toObjects(csv, { separator: "," })));
      });
  };
};

export const getTimezoneOffset = (profiles, date) => {
  let center = null;
  if (profiles && profiles.length > 0) {
    center = turf.center(
      turf.featureCollection(
        _.map(profiles, (profile) => {
          return turf.point([+profile.longitude, +profile.latitude]);
        })
      )
    ).geometry.coordinates;
  }
  if (center === null) {
    return {
      name: "UTC",
      offset: "Z",
    };
  }
  const timezone_name = tzlookup(center[1], center[0]);
  const timezome_offset = moment(date + "T12:00:00Z")
    .tz(timezone_name)
    .toISOString(true)
    .split(".000")[1];
  return {
    name: timezone_name,
    offset: timezome_offset,
  };
};

