export const fetchTour = ({ tour, formData, profiles }) => {
  return (dispatch) => {
    const currentProfile = _.find(profiles, { name: tour.typeId });

    const viaPoints = _.map(
      tour.stops.slice(1, tour.stops.length),
      (stop, key) => {
        return "via=" + stop.location.lat + "," + stop.location.lng;
      }
    );

    const timezone = getTimezoneOffset(
      _.filter(profiles, { active: true }),
      formData.startDate
    ).offset;

    const currentVehicle = _.find(
      VEHICLES.options,
      (vehicle) => +vehicle.id === +currentProfile.vehicleType
    );

    const destinationPoint = currentProfile.mustGoBack
      ? currentProfile.latitude + "," + currentProfile.longitude
      : tour.stops[tour.stops.length - 1].location.lat +
        "," +
        tour.stops[tour.stops.length - 1].location.lng;

    const urlQuery =
      "https://router.hereapi.com/v8/routes?" +
      `departureTime=${
        formData.startDate +
        "T" +
        currentProfile.timeWindow[0] +
        ":00" +
        timezone
      }&` +
      `origin=${currentProfile.latitude + "," + currentProfile.longitude}&` +
      `transportMode=${currentVehicle.type}&` +
      `destination=${destinationPoint}&` +
      `${viaPoints.join("&")}&` +
      "return=polyline";

    fetch(urlQuery, {
      method: "GET",
      headers: {
        Authorization: "Bearer " + Cookies.get("bearer_token"),
      },
    })
      .then((response) => response.json())
      .then((json) => {
        const decodedRouteSections = _.map(
          json.routes[0].sections,
          (section) => {
            return {
              ...section,
              polyline: polylineDecode.decode(section.polyline),
            };
          }
        );

        dispatch(
          getTour({
            ...json.routes[0],
            sections: decodedRouteSections,
            vehicleId: tour.vehicleId,
          })
        );
      })
      .catch((error) => {
        dispatch(
          setErrorMessage({
            title: "router.hereapi error",
            message: String(error),
          })
        );
      });
  };
};

export const fetchTours = ({ routingProblem, formData, profiles }) => {
  return (dispatch) => {
    _.map(routingProblem.tours, (tour) => {
      dispatch(fetchTour({ tour, formData, profiles }));
    });
  };
};

export const fetchRoutingProblem = ({ jobList, formData, profiles }) => {
  return (dispatch) => {
    dispatch(startFetchRoutingProblem());

    const timezone = getTimezoneOffset(
      _.filter(profiles, { active: true }),
      formData.startDate
    ).offset;
    dispatch(
      setFormData({
        timezone: getTimezoneOffset(
          _.filter(profiles, { active: true }),
          formData.startDate
        ),
      })
    );

    const requestBody = {
      plan: {
        jobs: _.map(jobList, (job) => {
          return {
            id: job.JobID,
            places: {
              delivery: {
                location: { lat: +job.latitude, lng: +job.longitude },
                times: [
                  [
                    `${formData.startDate}T${
                      job.TimeWindowStart && job.TimeWindowStart !== ""
                        ? job.TimeWindowStart
                        : "13:00"
                    }:00${timezone}`,
                    `${formData.startDate}T${
                      job.TimeWindowEnd && job.TimeWindowEnd !== ""
                        ? job.TimeWindowEnd
                        : "23:59"
                    }:00${timezone}`,
                  ],
                ],
                duration: +job.ServiceTimeMin * 60,
              },
            },
            demand: [+job.Demand],
            skills:
              job.SkillsRequired && job.SkillsRequired !== ""
                ? job.SkillsRequired.split(",")
                : undefined,
          };
        }),
      },
      fleet: {
        types: _.filter(
          _.map(profiles, (profile) => {
            if (profile.active) {
              const currentVehicle = _.find(
                VEHICLES.options,
                (vehicle) => +vehicle.id === +profile.vehicleType
              );
              return {
                id: profile.name,
                profile: profile.name,
                costs: {
                  distance: +profile.distanceCost,
                  time: +profile.timeCost,
                  fixed: +profile.fixedCost,
                },
                shifts: profile.mustGoBack
                  ? [
                      {
                        start: {
                          time: `${formData.startDate}T${profile.timeWindow[0]}:00${timezone}`,
                          location: {
                            lat: +profile.latitude,
                            lng: +profile.longitude,
                          },
                        },
                        end: {
                          time: `${formData.startDate}T${profile.timeWindow[1]}:00${timezone}`,
                          location: {
                            lat: +profile.latitude,
                            lng: +profile.longitude,
                          },
                        },
                      },
                    ]
                  : [
                      {
                        start: {
                          time: `${formData.startDate}T${profile.timeWindow[0]}:00${timezone}`,
                          location: {
                            lat: +profile.latitude,
                            lng: +profile.longitude,
                          },
                        },
                      },
                    ],
                limits:
                  !profile.maxDistance && !profile.shiftTime
                    ? undefined
                    : {
                        maxDistance: profile.maxDistance
                          ? +profile.maxDistance
                          : undefined,
                        shiftTime: profile.shiftTime
                          ? +profile.shiftTime
                          : undefined,
                      },
                capacity: [+profile.capacity],
                skills: profile.skills
                  ? _.map(profile.skills.split(","), (p) => p.trim())
                  : undefined,
                amount: +profile.vehicleCount,
              };
            }
          }),
          (p) => p !== undefined
        ),
        profiles: _.filter(
          _.map(profiles, (profile) => {
            if (profile.active) {
              const currentVehicle = _.find(
                VEHICLES.options,
                (vehicle) => +vehicle.id === +profile.vehicleType
              );
              return {
                name: profile.name,
                type: currentVehicle.type,
                departureTime: `${formData.startDate}T${profile.timeWindow[0]}:00${timezone}`,
              };
            }
          }),
          (p) => p !== undefined
        ),
      },
    };

    console.log(requestBody);

    fetch("https://vrp.api.here.com/v1/problems", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + Cookies.get("bearer_token"),
      },
      body: JSON.stringify(requestBody),
    })
      .then((response) => response.json())
      .then((json) => {
        if (json.status && json.status !== 200) {
          dispatch(
            setErrorMessage({
              title: "vrp.api.here: " + json.title,
              message: json.cause,
            })
          );
        } else {
          dispatch(getRoutingProblem(json));
          dispatch(fetchTours({ routingProblem: json, formData, profiles }));
        }
        dispatch(endFetchRoutingProblem());
      })
      .catch((error) => {
        dispatch(
          setErrorMessage({
            title: "vrp.api.here error",
            message: String(error),
          })
        );
        dispatch(endFetchRoutingProblem());
      });
  };
};
