//import {} from '../../redux/actions';
import "./styles.css";

import $ from "jquery";
import _ from "lodash";
import React, { useRef, useState } from "react";
import { Alert } from "react-bootstrap";
import Dropzone from "react-dropzone";

function UploadFileArea({ onDropAccepted }) {
  const refDropzone = useRef(null);
  const [errorMessages, setErrorMessages] = useState(null);
  const [uploadedFile, setUploadedFile] = useState(null);

  return (
    <div ref={refDropzone} className="dropzone-container">
      <Dropzone
        accept={".csv"}
        maxFiles={1}
        multiple={false}
        onDropAccepted={(files) => {
          $(refDropzone.current).find(".dropzone").removeClass("dragged");
          $(refDropzone.current).find(".dropzone").addClass("success");
          setErrorMessages(null);
          setUploadedFile(files[0]);
          onDropAccepted(files[0]);
        }}
        onDropRejected={(files) => {
          $(refDropzone.current).find(".dropzone").removeClass("dragged");
          $(refDropzone.current).find(".dropzone").addClass("error");
          setErrorMessages(
            _.map(files[0].errors, ({ message }, key) => (
              <Alert key={key} variant={"danger"}>
                {message}
              </Alert>
            ))
          );
        }}
        onDragLeave={(e) => $(e.target).removeClass("dragged")}
        onDragEnter={(e) => $(e.target).addClass("dragged")}
      >
        {({ getRootProps, getInputProps }) => (
          <div {...getRootProps({ className: "dropzone" })}>
            <input {...getInputProps()} />
            {!uploadedFile && (
              <p>
                Drag &apos;n&apos; drop .CSV file here, or click to select files
              </p>
            )}
            {uploadedFile && (
              <p className="success-message">
                Selected file: <span>{uploadedFile.name}</span>
              </p>
            )}
          </div>
        )}
      </Dropzone>
      {errorMessages && <div className="error-message">{errorMessages}</div>}
    </div>
  );
}

export default UploadFileArea;
