import { MAPBOX } from "../../constants";
import {
  MAPBOX_READY,
  MAPBOX_VIEWPORT_CHANGE,
  MOVE_TO_POINT,
  RESIZE_WINDOW,
  SET_CURRENT_TOUR,
} from "../actionTypes";

const initialState = {
  isMapboxReady: false,
  currentTourId: null,
  flyToPoint: null,
  viewport: {
    ...MAPBOX.VIEWPORT,
  },
};

export default function (state = initialState, action) {
  switch (action.type) {
    case MAPBOX_READY: {
      return {
        ...state,
        isMapboxReady: true,
      };
    }
    case RESIZE_WINDOW: {
      return {
        ...state,
        viewport: {
          ...state.viewport,
          width: action.payload.width,
          height: action.payload.height,
        },
      };
    }
    case MAPBOX_VIEWPORT_CHANGE: {
      return {
        ...state,
        viewport: {
          ...state.viewport,
          ...action.payload.viewport,
        },
      };
    }
    case SET_CURRENT_TOUR: {
      return {
        ...state,
        currentTourId: action.payload.tourId,
      };
    }
    case MOVE_TO_POINT: {
      return {
        ...state,
        flyToPoint: action.payload.coords,
      };
    }
    default:
      return state;
  }
}
